/*sidebar-filter*/
#sidebar-filter .select {
  cursor: pointer;
  position: relative;
}
#sidebar-filter .select:after {
  content: "";
  background: url('../images/filter_arrows.png') 0 6px;
  display: block;
  width: 13px;
  height: 6px;
  position: absolute;
  top: 12px;
  right: 6px;
}
#sidebar-filter .select.opened:after {
  background: url('../images/filter_arrows.png') 0 0;
}
#sidebar-filter .select+ul{
  display: none;
}
#sidebar-filter .select.opened+ul{
  display: block;
}
#sidebar-filter .select+ul > li.selected{
  color: #c21f13;
  cursor: pointer;
}
